<template>
  <el-breadcrumb
    class="app-breadcrumb"
    separator="/">
    <transition-group name="breadcrumb">
      <el-breadcrumb-item
        v-if='item.meta.title'
        v-for="(item, index) in levelList"
        :key="item.path"
        >
        <span
          v-if='item.redirect === "noredirect" || index == levelList.length - 1'
          class="no-redirect">
          {{item.meta.title}}
        </span>
        <router-link
          v-else
          :to="item.redirect || item.path">
          {{item.meta.title}}
        </router-link>
      </el-breadcrumb-item>
    </transition-group>
  </el-breadcrumb>
</template>

<script>

export default {
  created () {
    this.getBreadcrumb()
  },
  data () {
    return {
      levelList: null
    }
  },
  watch: {
    $route () {
      this.getBreadcrumb()
    }
  },
  methods: {
    getBreadcrumb () {
      let matched = this.$route.matched.filter(item => item.name)
      const first = matched[0]
      if (first && first.name !== 'Home' && matched[1].name !== 'Home') {
        matched = [{ path: '/home/index', meta: { title: '个人主页' } }].concat(matched)
      }
      this.levelList = matched
    }
  }
}
</script>

<style scoped>
  .app-breadcrumb.el-breadcrumb {
    display: inline-block;
    font-size: 14px;
    line-height: 50px;
    margin-left: 10px;
  }
  .no-redirect {
    color: #97a8be;
    cursor: text;
  }
</style>



